







































/**
 * 基础弹窗组件
 */
import { Component, Vue, Prop } from "vue-property-decorator";

@Component({
  name: "Open",
})
export default class Open extends Vue {
  @Prop({
    type: String,
    default: "确定",
  })
  saveText!: string;
  @Prop({
    type: String,
    default: "取消",
  })
  closeText!: string;
  @Prop({
    type: Boolean,
    default: false,
  })
  hideSave!: boolean;
  @Prop({
    type: Boolean,
    default: false,
  })
  hideClose!: boolean;
  @Prop({
    type: Boolean,
    default: false,
  })
  hideFoot!: boolean;
  @Prop({
    type: String,
    default: "信息",
  })
  title!: string;
  @Prop({
    type: [Number, String],
    default: 410,
  })
  width!: number | string;
  @Prop({
    type: [Number, String],
    default: "15vh",
  })
  top!: any;
  @Prop({
    type: String,
  })
  className!: string;
  @Prop({
    type: Boolean,
    default: false,
  })
  closeModal!: boolean;

  @Prop({
    type: Boolean,
    default: true,
  })
  modalShow!: boolean;

  @Prop({
    type: Boolean,
    default: false,
  })
  modalSaveEnable!: boolean; // 保存按钮是否可用

  @Prop({
    type: Boolean,
    default: false,
  })
  modelSaveLoading!: boolean;

  save() {
    this.$emit("on-save");
  }
  close() {
    this.$emit("on-close");
  }
  beforeCloseFun() {
    this.$emit("on-close");
  }
}
