






















import {Component, Prop, Vue} from 'vue-property-decorator'

@Component({
    name: 'LMenu'
})
export default class InsetBack extends Vue {
    @Prop({
        type: Array,
        default: ()=>{
            return [];
        }
    })
    leftMenu!: any;

    @Prop({
        type: Boolean,
        default: false
    })
    isCollapse!: boolean;

    toPage(path:string) {
        this.$emit('toPage', path)
    }
}
